  body {
            background-color: #282a36;
            color: #f8f8f2;
            font-family: 'Segoe UI', sans-serif;
            margin: 0;
            padding: 0;
            min-height: 100vh;
            display: flex;
            flex-direction: column;
        }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.ip-list {

  padding: 25px;
}

header {

  background-color: #21222c;
  padding: 1rem;
  border-bottom: 1px solid #44475a;
  display: flex;
  justify-content: right;
}

header h1 {
  font-family: 'Inter', 'Segoe UI', sans-serif;
  font-weight: 700;
  font-size: 1.2rem;
  color: #f8f8f2;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.highlight-box {
  background-color: #50fa7b1a;
  /* leichtes Gr√ºn als tech Akzent */
  color: #50fa7b;
  padding: 0.3em 0.8em;
  border-radius: 0.5rem;
  font-family: 'Roboto Mono', monospace;
  font-size: 1.1em;
  letter-spacing: 0.03em;
  border: 1px solid #50fa7b44;
  box-shadow: inset 0 0 4px #50fa7b33;
}

.highlight-box:hover {
  background-color: #50fa7b33;
  /* etwas dunkler beim Hover */
  box-shadow: inset 0 0 6px #50fa7b55;
}

.dashboard {
  flex: 1;
  display: flex;
  padding: 2rem;
  gap: 2rem;
  flex-wrap: wrap;
}

.input-card {
  background-color: #44475a;
  border-radius: 1rem;
  padding: 1.5rem;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  flex: 1 1 250px;
  /* schmaler */
}

.output-card {
  background-color: #44475a;
  border-radius: 1rem;
  padding: 1.5rem;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  flex: 2 1 450px;
  /* breiter */
}
